.navigation-menu{
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navigation-menu li{
    margin: 0px 20px;
}


.navigation-menu li a{
    text-decoration: none;
    color: white;
}


/* Mobile view */


.mobile-navigation li{
    margin-top: 20px;
}

.mobile-navigation li a {
    text-decoration: none;
    font-size: 1.4rem;
    color: black;
}

.active{
    color: goldenrod !important;
    font-weight: bold  !important;
}